<template>
  <div class="fillcontain">
    <h3>违规数据录入</h3>
    <div class="contain">
      <div class="table_container">
        <el-row class="readWrapper"></el-row>
        <el-table
          v-loading="loading"
          :data="tableData"
          border
          stripe
          highlight-current-row
          header-cell-class-name="table-header-class"
          style="width:100%"
        >
          <el-table-column label="序号" align="center">
            <template slot-scope="scope">
              <span>{{scope.$index+(paginations.pageIndex-1) * paginations.pageSize + 1}}</span>
            </template>
          </el-table-column>
          <!-- <el-table-column property="name" label="姓名" align="center"></el-table-column> -->
          <el-table-column property="batch_name" label="批次" align="center"></el-table-column>
          <el-table-column property="updateTime" label="时间段" :formatter="formatTime" align="center"></el-table-column>
          <!-- <el-table-column property="speed2" label="高度" align="center"></el-table-column>
          <el-table-column property="updateTime" label="球" align="center"></el-table-column>
          <el-table-column property="fruit" label="违规发射" align="center"></el-table-column>
          <el-table-column property="speed3" label="报告词" align="center"></el-table-column>
          <el-table-column property="cause" label="补充说明" align="center"></el-table-column>-->
          <el-table-column fixed="right" label="操作">
            <template slot-scope="scope">
              <!-- <el-button @click="handleClick(scope.row)" type="primary" size="small">查看</el-button> -->
              <el-button @click="editClick(scope.row)" type="primary" size="small">编辑</el-button>
              <!-- <el-button @click="handleClick(scope.row)" type="primary" size="small">删除</el-button> -->
            </template>
          </el-table-column>
        </el-table>
        <el-row>
          <el-col :span="24">
            <div class="pagination">
              <el-pagination
                v-if="paginations.total > 0"
                :page-sizes="paginations.pageSizes"
                :page-size="paginations.pageSize"
                :layout="paginations.layout"
                :total="paginations.total"
                :current-page="paginations.pageIndex"
                @current-change="handleCurrentChange"
                @size-change="handleSizeChange"
              ></el-pagination>
            </div>
          </el-col>
        </el-row>
      </div>
    </div>
    <el-dialog title="成绩录入" :visible.sync="dialogFormVisible2" class="customWidth">
      <el-form :model="form">
        <el-form-item label="选择批次：" :label-width="formLabelWidth" class="formLabelWidth2">
          <el-input v-model="form.batch_name" :disabled="true" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="对阵双方：" :label-width="formLabelWidth" class="formLabelWidth2">
          <el-input v-model="form.name" :disabled="true" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="对阵时间：" :label-width="formLabelWidth" class="formLabelWidth2">
          <el-input v-model="form.timeD" :disabled="true" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="武器数量：" class="inputNum" :label-width="formLabelWidth">
          <li>
            <span class="hpTitle">P-10:</span>
            <el-input-number size="mini" :min="0" :max="10" v-model="weapon[10]"></el-input-number>
          </li>
          <li>
            <span class="hpTitle">P-12:</span>
            <el-input-number size="mini" :min="0" :max="10" v-model="weapon[12]"></el-input-number>
          </li>
          <li>
            <span class="hpTitle">P-15:</span>
            <el-input-number size="mini" :min="0" :max="10" v-model="weapon[15]"></el-input-number>
          </li>
          <li>
            <span class="hpTitle">航炮:</span>
            <el-input-number size="mini" :min="0" :max="10" v-model="weapon[255]"></el-input-number>
          </li>
        </el-form-item>
        <p class="wgTitle">违规情况:</p>
        <div class="wgButtom">
          <el-form-item label="违规发射：" :label-width="formLabelWidth">
            <div class="block">
              <el-cascader
                @change="handleAreaChange"
                :options="optionsFS"
                :props="{ multiple: true, checkStrictly: true ,value: 'id',
		label: 'info'}"
                clearable
              ></el-cascader>
            </div>
          </el-form-item>
          <div class="inputNum2">
            <li>
              <span class="hpTitle">高度违规:</span>
              <el-input-number size="mini" :min="0" :max="10" v-model="form.height"></el-input-number>
            </li>
            <li>
              <span class="hpTitle">突破安全球:</span>
              <el-input-number size="mini" :min="0" :max="10" v-model="form.ball"></el-input-number>
            </li>
            <li>
              <span class="hpTitle">报告词违规:</span>
              <el-input-number size="mini" :min="0" :max="10" v-model="form.report"></el-input-number>
            </li>
            <li>
              <span class="hpTitle">其他违规:</span>
              <el-input-number size="mini" :min="0" :max="10" v-model="form.other"></el-input-number>
            </li>
          </div>
          <el-form-item label="补充说明：" :label-width="formLabelWidth">
            <el-input type="textarea" v-model="about" autocomplete="off"></el-input>
          </el-form-item>
        </div>

        <!-- <el-form-item label="报告词违规：" :label-width="formLabelWidth">
          <el-input v-model="form.report" autocomplete="off"></el-input>
        </el-form-item>-->

        <!-- <el-form-item label="其他违规：" :label-width="formLabelWidth">
          <span>多个违规逗号隔开</span>
          <el-input type="textarea" v-model="form.other" autocomplete="off"></el-input>
        </el-form-item>-->
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button class="userButModle" @click="dialogFormVisible2 = false">取 消</el-button>
        <el-button class="userButModle" type="primary" @click="add">确定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { ccList, setfire, commit } from "@/api/batch";
export default {
  data() {
    return {
      valueDate: "",
      nameSearch: "",
      value: "",
      form: {
        ball: 0,
        height: 0
      },
      weapon: {
        10: 0,
        12: 0,
        15: 0,
        255: 0
      },
      user: this.sessionData("get", "user"),
      dialogFormVisible2: false,
      formLabelWidth: "120px",
      tableData: [],
      optionsFS: [],
      about: "",
      loading: false,
      paginations: {
        total: 0, // 总数
        pageIndex: 1, // 当前位于哪页
        pageSize: 10, // 1页显示多少条
        pageSizes: [5, 10, 15, 20], //每页显示多少条
        layout: "total, sizes, prev, pager, next, jumper" // 翻页属性
      },
      selectId: ""
    };
  },
  mounted() {
    this.ccList({
      count: this.paginations.pageSize,
      index: this.paginations.pageIndex,
      uid: this.user._id,
      search: {
        state: 1
      }
    });
  },
  methods: {
    handleClick(i) {},
    handleAreaChange(val) {
      this.form.fire = val;
    },
    add() {
      var data = {
        _id: this.selectId,
        uid: this.user._id,
        weapon: this.weapon,
        foul: {
          height: this.form.height,
          ball: this.form.ball,
          report: this.form.report,
          fire: this.form.fire || [],
          other: Number(this.form.other || 0) //this.form.other.constructor == String? this.form.other.split(","): this.form.other
        },
        about: this.about
      };
      commit(data).then(res => {
        if (res.code == 0) {
          this.$message({
            message: "违规数据录入成功！",
            type: "success"
          });
          this.ccList({
            count: this.paginations.pageSize,
            index: this.paginations.pageIndex,
            uid: this.user._id,
            search: {
              state: 1
            }
          });
          this.dialogFormVisible2 = false;
        }
      });
    },
    setfire(id) {
      this.selectId = id;
      setfire({
        _id: id,
        uid: this.user._id
      }).then(res => {
        if (res.code == 0) {
          var list = this.$unfly(res.data);
          console.log("违规发射", list);
          list.forEach(i => {
            i.info = i.miss + "--" + this.$tool.formatDate(new Date(i.time));
          });
          this.dialogFormVisible2 = true;
          this.optionsFS = list;
        }
      });
    },

    formatTime(row) {
      row.timeD =
        this.$tool.formatDate(new Date(row.start)) +
        "--" +
        this.$tool.formatDate(new Date(row.end));
      return row.timeD;
    },

    editClick(row) {
      this.setfire(row._id);
      this.form = row;
    },

    ccList(obj) {
      var data = this.$tool.deep(obj);
      data.index -= 1;
      ccList(data).then(res => {
        if (res.code == 0) {
          var list = this.$unfly(res.data);
          this.loading = false;
          this.paginations.total = list.total;

          var user = list.users;
          list.array.forEach(i => {
            i.name = "";
            i.loser.forEach(j => {
              i.name += user[j];
              i.name += " ";
            });
            i.name += "VS ";
            i.winner.forEach(j => {
              i.name += user[j];
              i.name += " ";
            });
          });
          console.log("88888888888888", list);
          this.tableData = list.array;
          this.paginations.total = list.total;
        }
      });
    },

    // 每页多少条切换
    handleSizeChange(pageSize) {
      this.paginations.pageSize = pageSize;
      this.ccList({
        count: this.paginations.pageSize,
        index: this.paginations.pageIndex,
        search: {
          state: 1
        }
      });
    },
    // 上下分页
    handleCurrentChange(page) {
      this.paginations.pageIndex = page;
      this.ccList({
        count: this.paginations.pageSize,
        index: this.paginations.pageIndex,
        search: {
          state: 1
        }
      });
    }
  }
};
</script>

